    ul.share a.share__facebook {
      position: relative;
      padding-left: calc(16px + 7px + .28em); }
      ul.share a.share__facebook:before {
        content: "";
        text-align: center;
        position: absolute;
        top: 50%;
        left: 7px;
        transform: translate(0, -50%);
        width: 16px;
        height: 16px;
        background: url(https://www.google.com/s2/favicons?domain=https://www.facebook.com/) no-repeat scroll center center transparent;
